@use "@/styles/mixins";

.content {
  grid-area: content;
  position: relative;
  margin-bottom: 100px;
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  background: linear-gradient(
    180deg,
    rgba(247, 247, 247, 0.8) 0%,
    rgba(247, 247, 247, 0.5) 10.69%
  );

  @include mixins.lg {
    margin-bottom: 160px;
    padding: 52px;
  }

  &__header {
    display: flex;
    flex-direction: column;
    gap: 24px;
    border-bottom: 1px solid var(--white-40);
    padding: 24px;

    @include mixins.lg {
      flex-direction: row;
      padding: 0 0 30px;
    }

    & > * {
      flex: 1 1 0px;
    }
  }

  &__main {
    display: flex;
    flex-direction: column;
    margin: 24px 0 0 0;
    padding: 0 8px;

    & > * {
      padding: 0 0 24px 0;
      display: flex;
      flex-direction: column;
      gap: 24px;
    }
  }
}
